@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster+Two:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700;800;900&display=swap);
:root {
  --body-font: 'Lato', sans-serif;
  --subheading-font: 'Lobster Two', cursive;
  --heading-font: 'Lobster', cursive;
  --btn-color: forestgreen;
  /* --brown: #80461B; */
  --brown:#8B4513;
  --brown2:#A0522D;
  --green: forestgreen;
  --white: #E7E7E9;
  --subheading-font2: 'Oswald', sans-serif;
  /* --green: #2E8B57; */

--paper-color: #F1EDE9;
--paper-line: #94ACD4;
}

*{
  box-sizing: border-box;
}

.flagship-btn {
  background-color: #8B4513;
  background-color: var(--brown);
  font-family: 'Lato', sans-serif;
  font-family: var(--body-font);
  font-weight: 700;
  width: 175px;
  height: 65px;
  color: white;
  border-radius: 30px;
  border: 1px solid #8B4513;
  border: 1px solid var(--brown);
}

.flagship-btn:hover {
  background-color: white;
  color: #8B4513;
  color: var(--brown);
  border: 1px solid #8B4513;
  border: 1px solid var(--brown)
}
.flagship-btn:active .flagship-btn:focus, .flagship-btn.focus { 
  background-color: #8B4513; 
  background-color: var(--brown);
  box-shadow: none;
}


.green-btn{
  border-radius: 30px; background-color: forestgreen; background-color: var(--green);
  width: 175px;
  color: white;
  font-family: 'Lato', sans-serif;
  font-family: var(--body-font);
  padding: 1rem 2.5rem;
  text-align: center;
  border: 2px solid forestgreen;
  border: 2px solid var(--green);
  opacity: .75;
}

.green-btn:hover {
  background-color: white;
  opacity: .9;
  color: forestgreen;
  color: var(--green);
}



body {
background-color:#ececec;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header-container {
    position: relative;
    background-color: var(--white);
    border-bottom: 5px solid var(--green);
    height: 20vh;
}

.logo-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 15vh;
    position: relative;
    left: 20px;

    cursor: pointer;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
}

.header-title {
    font-family: var(--subheading-font);
    font-size: 5rem;
    position: relative;
    top: 5px;
    color: var(--green);
    font-weight: 500;

}

.header-leaf {
    height: 65px;
}

.header-user-container {
    display: flex;
    position: absolute;
    right: 40px;
    top: 20px;
}

.header-user-container:hover {

    cursor:pointer;
}

.header-username {
    margin-right: 0.5rem;
    position: relative;
    top: 5px;
    font-weight: 600;
    font-family: var(--body-font);
    color: var(--brown)
}


.header-user-icon {
    width: 32.25px;
    position: relative;
    bottom: 0.8px;
    right: 1.5px;
}

.header-user-icon-container {
    height: 35px;
    width: 35px;
    border: 3px solid green;
    border-radius: 100%;
    overflow: hidden;
}

.header-user-collapse {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: absolute;
    right: 40px;
    top:60px;
    background-color: white;
    border: 1px solid green;
    padding: 0.5rem;
    border-radius: 5px;

}
    
.header-user-profile-btn, .header-user-logout-btn, .header-user-wiki-btn {
    border: none;
    font-weight: 500;
    background-color: white;
    margin: 0.25rem;
    font-family: var(--body-font);
    }


    .header-user-profile-btn:hover, .header-user-logout-btn:hover, .header-user-wiki-btn:hover {
        text-decoration: underline;
        color: green;
    }

#header-center {
    height: 20vh;
}

.navbar-container {
    margin: auto;
    left: 0;
    right: 0;
    bottom: 32px;
    background-color: transparent;
    height: 0vh;
    color:var(--green);
    position: absolute;
}

.navbar-link{
    background-color: transparent;
    color: var(--green);
    display: inline-block;
    width: 200px;
    height: 32px;
    text-decoration: none;
    padding-top: 5px;
    letter-spacing: 0.25px;
    font-weight: 500;
    font-family: system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    font-size: 1.15rem;
}

.navbar-link:hover {
    color: var(--green);
}

.active {
    background-color: var(--green);
    color: var(--white);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.active:hover {
    color: var(--white);
}


