    align-content: center;
    align-items: center;
    grid-gap: 25px;
    gap: 25px;
    margin: 0rem auto 2rem auto;
    max-width: 1200px;
}

.garden-list-add-btn {
    margin-bottom: 2rem;
}

.garden-list-main-heading{
    font-family: var(--subheading-font2);
    margin-top: 3rem;
