}

.garden-detail-background-container {
    background-color: white;
    max-width: 950px;
    margin: 3rem auto 4rem auto;
    padding-top:1rem;
    border-radius: 15px;
    opacity: .75;
}

.garden-detail-upper-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
    max-width:1000px;
    grid-gap: 1rem;
    gap: 1rem;
    padding-top:2rem;
    justify-content: center;
    justify-items: center;
