.garden-detail-rename-input {
    width: 200px;
}

.garden-detail-rename-btn {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    background-color: var(--green);
    opacity: 0.9;
}

.garden-detail-rename-btn:hover {
