.notepad {
  margin-left: 40px;
  width: 400px;
  box-shadow: 10px 10px 40px rgba(black, .15);
  border-radius: 0 0 10px 10px;
  overflow: hidden;

}

.top {
  width: 100%;
  height: 50px;
  background: #333;
  border-radius: 5px 5px 0 0;
  text-align: right;
  padding-right: 1rem;
  padding-top: 0.25rem;
  font-size: 1.25rem;
}

.paper {
  width: 100%;
  height: 100%;
  padding: 0px 20px;
  background: repeating-linear-gradient(#F3E779, #F3E779 31px, #94ACD4 31px, #94ACD4 32px);
  font-family: var(--body-font);
  line-height: 32px;
  outline: 0;
  overflow-wrap: break-word;
  border: none;
  overflow: visible;
  display: block;
}


.garden-note-container {
  position: relative;
}



.garden-notes-save-btn {
  color :white;
}

.garden-notes-add-btn {
  color: white;
}

.garden-notes-delete-btn {
position: absolute;
top: 2px;
right: 5px;
z-index: 2;
}

.garden-notes-delete-btn:hover {
  color: tomato;
}

.garden-notes-heading {
  padding-bottom: 1.5rem;
}

.garden-notes-heading:after {
  content: ""; /* This is necessary for the pseudo element to work. */ 
  display: block; /* This will put the pseudo element on its own line. */
  margin: 0 auto; /* This will center the border. */
  width: 20%; /* Change this to whatever width you want. */
  padding-top: 8px; /* This creates some space between the element and the border. */
  border-bottom: 2px solid lightgrey; /* This creates the border. Replace black with whatever color you want. */
}